function constantsToString(constants) {
  var results = [];

  for (var p in constants) {
    var constant = constants[p];

    switch (typeof constant) {
      case 'number':
      case 'boolean':
        results.push("".concat(p, ":").concat(constant));
    }
  }

  return "{ ".concat(results.join(), " }");
}

